<template>
  <div class="container">
    <h2>Hashing - MD5</h2>
    <p></p>
    <div class="ui card">
      <div class="content">
        <h4 class="ui sub header">Original CSS</h4>
        <div class="ui input">
          <textarea
            placeholder="Enter CSS..."
            rows="20"
            spellcheck="false"
            v-model="input"
            @keypress="generate"
            @change="generate"
          ></textarea>
        </div>
        <h4 class="ui sub header">Transformed CSS</h4>
        <pre>
          <code>{{ output }}</code>
        </pre>
      </div>
    </div>
  </div>
</template>

<script>
import postcss from 'postcss';
import autoprefixer from 'autoprefixer';

export default {
  data() {
    return {
      input: '',
      output: '',
    };
  },
  methods: {
    async generate() {
      const result = await postcss([autoprefixer]).process(this.input);
      this.output = result.css;
    },
  },
};
</script>

<style scoped>
.ui.card {
  width: 100%;
}

.ui.input,
.ui.input textarea {
  width: 100%;
}

.ui.input textarea {
  resize: vertical;
  padding: 0.5rem;
  font-family: 'Courier New', Courier, monospace;
}
pre {
  width: 100%;
  margin: 0 !important;
  text-align: left;
}
pre code {
  background-color: #eee;
